import React from 'react';

export function AppHeader({ controlBarOpen, setControlBarOpen }) {
  const handleToggleControls = () => {
    setControlBarOpen(!controlBarOpen);
  };

  return (
    <div style={{ marginBottom: 20, textAlign: 'center' }}>
      <h1 style={{ color: '#333', marginBottom: 10 }}>Urban Health and Livability Visualization</h1>
      <button
        onClick={handleToggleControls}
        style={{
          padding: '10px 20px',
          backgroundColor: controlBarOpen ? '#dc3545' : '#28a745',
          color: 'white',
          border: 'none',
          borderRadius: '5px',
          cursor: 'pointer',
          fontSize: '16px',
          marginBottom: 20
        }}
      >
        {controlBarOpen ? 'Hide Controls' : 'Show Controls'}
      </button>
    </div>
  );
} 